<template>
  <div>
    <h1 class="pb-2">OUR PRODUCTS</h1>
    <br />
    <p>
      This is our catalogue, every item here is in stock, but since we still
      don't have a wallet system, it is not possible to purchase any of them. If
      you feel like you want to buy one of our products, feel free to
      <b-link href="#/Contact">Contact Us</b-link> an we will send you our
      product as soon as possible.
    </p>
    <br />

    <div class="container-md">
      <b-card-group columns>
        <div columns v-for="proyecto in proyectos" :key="proyecto.id">
          <Card
            :price="proyecto.price"
            :srcimg="proyecto.srcimg"
            :alt="proyecto.alt"
            :titulo="proyecto.titulo"
            :texto="proyecto.texto"
            :url="proyecto.url"
          ></Card>
        </div>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import Card from "../components/Card.vue";

const lstProysJson = "../ProductList.json";

export default {
  props: {
    name: {
      type: String,
      default: "Products",
    },
  },
  components: {
    Card,
  },
  data() {
    return {
      proyectos: [],
      page: 1,
      loading: false,
      perPage: 20,
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      const res = await fetch(lstProysJson);
      const val = await res.json();
      this.proyectos = val;
      console.log(val);
    },
  },
};
</script>